.wp-block-create-block-sermon-manager {
	background-color: #21759b;
	color: #fff;
	padding: 2px;

}


// Sermon container
.sermons-container {
	color: black;
	background-color: #FFFFFF;
	font-family: poppins, sans-serif;
	padding: 10px 5px
}

.sermon-posts-inner {

	max-width: 960px;
	margin: 0 auto;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
	gap: 20px;
	object-fit: contain;
  	object-position: center;
}

.sermon-post {
	padding: 10px;
	border: 1px solid #C8D7D1;
	// background-color:  #F7F7F7;
}


.sermon-post div:first-child {
	position: relative;
	min-height: 50px;
	padding: 50px 20px;
	color: #FFFFFF;
	object-fit: contain;
	// background-size: cover; /* Scales the image to cover the entire container */
  	// background-position: center center; /* Centers the image horizontally and vertically */
  	background-repeat: no-repeat; /* Prevents the image from repeating */
  	// background-attachment: fixed; /* Fixes the background relative to the viewport, creating a parallax-like effect when scrolling */
  	min-height: 15vh; /* Ensures the body always takes up at least the full viewport height */
  	margin: 0; /* Removes default body */
	
}

.sermon-post div:first-child::before {
	content: ""; /* Essential for pseudo-elements */
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(3, 28, 51, 0.65); /* Black with 65% opacity */
	z-index: 2; /* Ensures it's above the content */
}

.sermon-post div:first-child  h2 a {
	position: relative;
    z-index: 10;
	font-size: 18px;
	font-family:  bold;
	text-decoration: none;
	padding-top: 30px;
	text-decoration: none;
	line-height: 1.2;
}

.sermon-post div:nth-child(2) {
	padding-top: 20px;
}

.sermon-post div:nth-child(2) a {
	font-size: 14px;
	text-decoration: none;
	pointer-events: none;
}

.hgi-video-01, .hgi-music-note-square-02 {
	font-size: 30px;
	padding: 10px;
}